
.seller {
  position: fixed;
  top: 174px;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
  position: relative;
  padding: 18px;
}
.seller .overview .title {
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .overview .star-content {
  margin: 8px 0 18px 0;
  font-size: 0;
}
.seller .overview .star-content .star {
  display: inline-block;
  vertical-align: top;
}
.seller .overview .star-content .ratingCount {
  display: inline-block;
  vertical-align: top;
  margin: 0 12px 0 8px;
  font-size: 10px;
  line-height: 18px;
  color: #4d555d;
}
.seller .overview .star-content .sellCount {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  line-height: 18px;
  color: #4d555d;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.seller .overview .remark .block.block:nth-child(2) {
  border-right: 1px solid rgba(7,17,27,0.1);
  border-left: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block .title {
  text-align: center;
  font-size: 10px;
  line-height: 10px;
  color: #93999f;
}
.seller .overview .remark .block .price {
  margin-top: 4px;
  text-align: center;
  font-size: 0;
}
.seller .overview .remark .block .price .price1 {
  font-size: 24px;
  font-weight: 200;
  line-height: 24px;
  color: #07111b;
}
.seller .overview .remark .block .price .price2 {
  font-size: 10px;
}
.seller .overview .favorite {
  position: absolute;
  width: 50px;
  right: 18px;
  top: 18px;
  text-align: center;
}
.seller .overview .favorite .icon-favorite {
  display: block;
  font-size: 24px;
  line-height: 24px;
  color: #93999f;
}
.seller .overview .favorite .icon-favorite.active {
  color: #f01414;
}
.seller .overview .favorite .text {
  display: block;
  font-size: 10px;
  line-height: 10px;
  color: #4d555d;
}
.seller .bulletin {
  padding: 18px 18px 0;
}
.seller .bulletin .maintext {
  padding-bottom: 16px;
}
.seller .bulletin .maintext .title {
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .bulletin .maintext .text {
  padding: 0 12px;
  font-size: 12px;
  font-weight: 200;
  line-height: 24px;
  color: #f01414;
}
.seller .bulletin .supports {
  padding: 0 12px;
}
.seller .bulletin .supports .supports-item {
  padding: 16px 0;
  font-size: 0;
}
.seller .bulletin .supports .supports-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.seller .bulletin .supports .supports-item .icon.decrease {
  background-image: url("decrease_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item .icon.decrease {
    background-image: url("decrease_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .icon.discount {
  background-image: url("discount_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item .icon.discount {
    background-image: url("discount_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .icon.guarantee {
  background-image: url("guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item .icon.guarantee {
    background-image: url("guarantee_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .icon.invoice {
  background-image: url("invoice_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item .icon.invoice {
    background-image: url("invoice_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .icon.special {
  background-image: url("special_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item .icon.special {
    background-image: url("special_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .text {
  display: inline-block;
  vertical-align: top;
  margin-left: 6px;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  color: #07111b;
}
.seller .pics {
  padding: 18px 0 18px 18px;
}
.seller .pics .title {
  margin-bottom: 12px;
  font-size: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .pics .pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.seller .pics .pic-wrapper .pic-list {
  font-size: 0;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.seller .info {
  padding: 0 18px;
}
.seller .info .title {
  padding: 12px 0;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .info .info-item {
  padding: 16px 12px;
  position: relative;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  color: #07111b;
}
.seller .info .info-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}


