
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
}
.food.food-enter-active,
.food.food-leave-active {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.food-enter,
.food.food-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .img-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .img-header .image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .img-header .back {
  position: absolute;
  left: 0;
  top: 0;
  color: #fff;
  font-size: 24px;
}
.food .content {
  padding: 18px;
  position: relative;
}
.food .content .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.food .content .detail {
  margin-top: 8px;
  margin-bottom: 18px;
  font-size: 0;
  line-height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .content .price .now {
  font-size: 14px;
  color: #f01414;
}
.food .content .price .old {
  text-decoration: line-through;
  margin-left: 8px;
  font-size: 10px;
  color: #93999f;
}
.food .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .add-food {
  position: absolute;
  right: 18px;
  bottom: 18px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 24px;
  line-height: 24px;
  background: #00a0dc;
  border-radius: 12px;
  text-align: center;
}
.food .content .add-food .add {
  font-size: 10px;
  line-height: 12px;
  color: #fff;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  padding: 0 8px;
  line-height: 24px;
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
}
.food .rating {
  padding-top: 18px;
}
.food .rating .title {
  margin-left: 18px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.food .rating-wrapper {
  padding: 0 18px;
}
.food .rating-wrapper .have-rating .rating {
  position: relative;
  padding: 16px 0;
}
.food .rating-wrapper .have-rating .rating .time {
  margin-bottom: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.food .rating-wrapper .have-rating .rating .content {
  font-size: 0;
}
.food .rating-wrapper .have-rating .rating .content .icon-thumb_up,
.food .rating-wrapper .have-rating .rating .content .icon-thumb_down {
  margin-right: 4px;
  font-size: 12px;
  lint-height: 24px;
}
.food .rating-wrapper .have-rating .rating .content .icon-thumb_up.icon-thumb_up,
.food .rating-wrapper .have-rating .rating .content .icon-thumb_down.icon-thumb_up {
  color: #00a0dc;
}
.food .rating-wrapper .have-rating .rating .content .icon-thumb_up.icon-thumb_down,
.food .rating-wrapper .have-rating .rating .content .icon-thumb_down.icon-thumb_down {
  color: #93999f;
}
.food .rating-wrapper .have-rating .rating .content .text {
  font-size: 12px;
  color: #07111b;
  line-height: 16px;
}
.food .rating-wrapper .have-rating .rating .user {
  position: absolute;
  top: 16px;
  right: 0;
  font-size: 0;
}
.food .rating-wrapper .have-rating .rating .user .userName {
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.food .rating-wrapper .have-rating .rating .user .avatar {
  vertical-align: top;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.food .rating-wrapper .no-rating {
  padding: 16px 0;
}
.food .rating-wrapper .no-rating .text {
  font-size: 12px;
  line-height: 16px;
  color: #93999f;
}


