
.ratingselect .select-type {
  margin: 0 18px;
  padding: 18px 0;
  font-size: 0;
  position: relative;
}
.ratingselect .select-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.ratingselect .select-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 2px;
  font-size: 12px;
  line-height: 16px;
  color: #4d555d;
}
.ratingselect .select-type .block.active {
  color: #fff;
}
.ratingselect .select-type .block .count {
  margin-left: 2px;
  font-size: 8px;
}
.ratingselect .select-type .block.all {
  background: rgba(0,160,220,0.2);
}
.ratingselect .select-type .block.all.active {
  background: #00a0dc;
}
.ratingselect .select-type .block.posi {
  background: rgba(0,160,220,0.2);
}
.ratingselect .select-type .block.posi.active {
  background: #00a0dc;
}
.ratingselect .select-type .block.nega {
  background: rgba(77,85,93,0.2);
}
.ratingselect .select-type .block.nega.active {
  background: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  color: #93999f;
  position: relative;
}
.ratingselect .switch:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.ratingselect .switch .icon {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.ratingselect .switch .icon.on {
  color: #00c850;
}
.ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}


